html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}
.body_style{
  width: 1140px;
  margin: 0 auto;
}
.container {
  display: flex;
  justify-content: space-between;
  justify-content: center;
  align-items: center;
}

.container_header {
  display: flex;
  justify-content: space-between;
  margin-top: 54px;
}

.hero_header {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 56px;
  line-height: 136%;
  color: #202220;
  margin-bottom: 25px;
}

.hero_desc {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 180%;
  color: #6b7280;
  width: 555px;
  margin-bottom: 85px;
}

.logo {
  display: block;
  margin-right: 583px;
}

#header-menu {
  display: flex;
  flex-direction: row;
  gap: 35px;
  list-style: none;
}

li {
  a {
    font-family: "Poppins", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    text-decoration: none;
    color: #6b7280;

    &:hover {
      color: #312e81;
    }
  }

  &.current_page_item {
    a {
      color: #312e81;
      font-weight: 500px;
    }
  }
}

.menu_class {
  gap: 35px;
}

.header_first_button {
  border-radius: 8px;
  padding: 12px 28px;
  width: 178px;
  height: 58px;
  background: #312e81;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: #fff;
  display: inline-flex;
  align-items: center;
  gap: 12px;
  border-radius: 8px;

  &:hover {
    background-color: dark;
    color: white;

    svg {
      path {
        fill: white;
      }
    }
  }
}

.btn {
  justify-content: center;
  border-radius: 8px;
  padding: 12px 28px;
  height: 58px;
  display: inline-flex;
  align-items: center;
  gap: 12px;
  border-radius: 8px;
  line-height: 150%;
  width: 178px;

  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: #fff;
}
.header_first_button {
  background: #2f1fa8;
  color: #fff;
  margin-right: 25px;

  &:hover {
    background-color: #312e81;
    color: white;

    svg {
      path {
        fill: white;
      }
    }
  }
}

.header_second_button {
  background: #dfdfdf;
  color: #6b7280;


  &:hover {
    background-color: #686868;
    color: white;

    svg {
      path {
        fill: white;
      }
    }
  }
}

.hero_img {
  width: 539px;
  height: 450px;
}
